.header {

    padding-top: 50px;
    position: relative;

}


.header ul {

    display: flex;
    justify-content: space-between;
    font-size: 12px;

}

.nav-bar_first {

    max-width: 265px;
    width: 100%;

}

.nav-bar_second {

    max-width: 137px;
    width: 100%;

}

.nav-menu {

    display: flex;
    align-items: center;
    justify-content: space-between;

}

.nav-menu-list {

    max-width: 265px;
    width: 100%;

}

.nav-menu-list-second {

    max-width: 137px;
    width: 100%;

}

.nav-menu__link {

    color: #fff;

}

.nav-menu__link_second {

    color: #808080;

}

.hamburger {

    display: none;

}

.hamburger .line{
    width: 30px;
    height: 2px;
    border-radius: 6px;
    background-color: #ecf0f1;
    display: block;
    margin: 4px auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    z-index: 12;
  }

  .hamburger:hover{
    cursor: pointer;
  }

  #hamburger-1.is-active .line:nth-child(2){
    opacity: 0;
  }
  
  #hamburger-1.is-active .line:nth-child(1){
    -webkit-transform: translateY(6px) rotate(45deg);
    -ms-transform: translateY(6px) rotate(45deg);
    -o-transform: translateY(6px) rotate(45deg);
    transform: translateY(6px) rotate(45deg);
  }
  
  #hamburger-1.is-active .line:nth-child(3){
    -webkit-transform: translateY(-6px) rotate(-45deg);
    -ms-transform: translateY(-6px) rotate(-45deg);
    -o-transform: translateY(-6px) rotate(-45deg);
    transform: translateY(-6px) rotate(-45deg);
  }


  /* RESPONSIVE */

  @media(max-width: 767px) {

    .nav-bar_first {

        display: none;
        position: absolute;
        bottom: -380%;
        left: 0;
        z-index: 99;
        max-width: none;
        padding: 30px 0;
        background-color: RGBA(128,128,128,0.81);
        overflow: hidden;

    }
    .nav-bar_first.is-active {

        display: block;
        animation: dropDown 0.5s ease-in-out;
        transition: all 0.5s ease;

    }

    @keyframes dropDown {
        0% {
            opacity: 0;
            transform: translateY(-10%);
        }
        100% {
            opacity: 1;
            transform: translateY(0%);
        }
    }

    .nav-menu-list {

        flex-direction: column;
        margin: 0 auto;

    }

    .nav-menu-list__item {

        font-size: 30px;
        text-align: center;

    }

    .nav-menu-list__item:not(:last-child) {

        margin-bottom: 20px;

    }

    .nav-bar_second {

        margin-left: auto;
        margin-right: 20px;

    }

    .hamburger {

        display: block;

    }

  }