
.footer {

    padding: 230px 0 130px 0;

}

.footer .logo {

    margin: 0 auto;

}

.socials-list {

    max-width: 544px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 44px auto 0;

}

.socials__link {

    color: #fff;
    font-weight: 400;
    font-size: 18px;

}

.questions__text {

    color: #808080;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    margin-top: 28px;

}

.mail {

    color: #808080;
    font-size: 18px;
    font-weight: 400;
    margin-left: 6px;

}

/* RESPONSIVE */ 

@media (max-width: 575px) {

    .socials-list {

        flex-direction: column;
        text-align: center;

    }

    .socials-list__item:not(:last-child) {

        margin-bottom: 20px;

    }

}