.hero__title {

    max-width: 382px;
    width: 100%;
    margin: 126px auto 0 ;

}

.hero__text {

    font-weight: 300;
    text-align: center;
    max-width: 268px;
    width: 100%;
    margin: 26px auto 0;

}

.line {

    border-right: 1px solid #fff;
    width: 1px;
    height: 65px;
    margin: 26px auto 0;

}

.hero__img {

    margin: 0 auto;
    
}

/* RESPONSIVE */

@media (max-width: 461px) {

    .hero__title {

        font-size: 40px !important;
        text-align: center;

    }
    
    .hero__text {

        font-size: 14px;

    }
}