
/* DEFAULT CSS */

html {

    scroll-behavior: smooth;

}

body {

    background-color: #010001;
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
    font-weight: 400;
    color: #fff;
    font-size: 16px;

}

* {

    box-sizing: border-box;
    margin: 0;
    padding: 0;

}

h1,h2,h3,h4,h5,h6 {

    margin: 0;
    padding: 0;

}

ul,li {

    margin: 0;
    padding: 0;
    list-style: none;

}

a {

    text-decoration: none;

}

button {

    margin: 0;
    padding: 0;
    border: none;
    background: none;
    font-family: inherit;

}

img {

    display: block;
    object-fit: cover;
    width: 100%;

}

/* DEFAULT CSS */

/* MAINTANCE CSS */

.container {

    max-width: 1240px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;

}


/* CSS */

.logo {

    background: url('../src/assets/logo.png') no-repeat center;
    width: 139px;
    height: 17px;
    display: block;

}

.main__title {

    font-size: 64px;
    color: #fff;
    font-weight: 400;

}

.section__subtitle {

    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;

}

.ghost__btn {

    font-size: 18px;
    color: #fff;
    border: 2px solid #fff;
    border-radius: 6px;
    width: 260px;
    padding: 20px 0;
    display: block;
    text-align: center;
    margin: 30px auto;

}