.ship-wrapper {

    display: flex;
    justify-content: space-between;
    align-items: center;

}

.ship-info__title {

    max-width: 177px;
    width: 100%;

}

.ship-info-table {

    border-collapse: collapse;
    width: 503px;
    margin-top: 45px;
    vertical-align: middle;
    margin-bottom: 200px;

}


.ship-info-table td {

    padding-bottom: 15px;

}



.ship-info-table__title {

    font-size: 18px;
    font-weight: 400;
    color: #fff;
    text-transform: uppercase;

}

.ship-info-table__value {

    margin-right: 5px;

}

.ship-info-table__value_1::after {

    content: '/';
    margin-left: 5px;

}

.ship-info-table__value_2 {

    color: #B2B0B2;

}

.ship-info__text {

    font-weight: 400;
    line-height: 26px;
    max-width: 490px;
    width: 100%;
    margin-top: 45px;

}

.ship__img {

    max-width: 365px;
    width: 100%;

}

/* RESPONSIVE  */

@media (max-width: 867px) {

    .ship-wrapper {

        flex-direction: column;
    
    }

    .ship__img {

        order: 1;

    }

    .ship-info-wrapper {

        order: 2;
        margin-top: 100px;
        overflow-x: scroll;

    }

    .ship-info__title {

        max-width: none;
        width: 100%;
        text-align: center;
    
    }

    .ship-info-table {

        border-collapse: collapse;
        max-width: 420px;
        width: 100%;
        margin: 0 auto;
        margin-top: 45px;
        margin-bottom: 200px;
        text-align: center;
        vertical-align: middle;
    
    }

    .ship-info-table__title {

        font-size: 12px;

    }

    .ship-info-table__value {

        font-size: 12px;

    }

    .ship-info__text {

        text-align: center;

    }

}